
import React, { useEffect, useState } from 'react';

export const Covid = () => {

    const [data,setdata] = useState([]);

    const getCovidData = async () => {
        const res = await fetch('https://data.covid19india.org/data.json');
        const Data = await res.json();
        console.log(Data.statewise[0]);
        setdata(Data.statewise[0]);
    }


    useEffect(() => {
        getCovidData();
    }, []);



    return (
        <div>
                 <h1 className="text-center  mt-3 heading"> COVID-19 TRACKER </h1>

            <div className="container d-flex  justify-content-center" >

              
                <span/>
                <br/>

                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">OUR COUNTRY</h5>
                        <p className="card-text">PAKISTAN</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>


                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">TOTAL RECOVERED</h5>
                        <p className="card-text">{data.recovered}</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>


                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">TOTAL CONFIRMED</h5>
                        <p className="card-text">{data.confirmed}</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>
                </div>

                <div className="container d-flex ml-5 justify-content-center" >

                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">TOTAL DEATH</h5>
                        <p className="card-text">{data.deaths}</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>


                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">TOTAL ACTIVE</h5>
                        <p className="card-text">{data.active}</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>


                <div className="card m-3" style={{ width: "18rem" }}>
                    <img src="https://media.istockphoto.com/photos/coronavirus-new-strain-wide-dark-background-picture-id1308624310?s=612x612" className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">LAST UPDATED</h5>
                        <p className="card-text">{data.lastupdatedtime}</p>
                        <a href="#" className="btn btn-primary">Read more</a>
                    </div>
                </div>

                </div>
        </div>
        
    )
}
